
.hero{background-image:url("../assets/img/hero-home-1.jpg")
}
.mission{background-image:url("../assets/img/mission-block-background.png");background-size:cover;background-position:center center;background-repeat:no-repeat;height:479px;margin-bottom:150px;position:relative
}
